<template>
  <div class="goods">
    <div class="menu-wrapper">
      <ul>
        <li v-for="item in goods" class="menu-item">
          <span class="text border-1px">
            <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper">
sda
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  const ERR_OK = 0

  export default{
      props:{
          seller:{
              type:Object
          }
      },
    data(){
        return{
            goods : []
        }
    },
      created () {
        //对应表
        this.classMap = ['decrease','discount','special','invoice','guarantee']
          this.$http.get('/api/goods').then((response) => {
              response = response.body

              if(response.errno ===ERR_OK){
                this.goods = response.data
                console.log(this.goods)

              }
        })
      }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/maxin.styl"
.goods
  display: flex
  position: absolute
  top 174px
  bottom 46px
  overflow hidden
  .menu-wrapper
    display: inline-block
    flex 0 0 80px
    width 80px
    background #F3F5F7
    .menu-item
      display: table
      height 54px
      padding 0 12px
      .icon
        display: inline-block
        width 16px
        height 16px
        vertical-align top
        margin-right 2px
        background-size 16px 16px
        background-repeat no-repeat
        &.decrease
          bg-img('decrease_3')
        &.discount
          bg-img('discount_3')
        &.guarantee
          bg-img('guarantee_3')
        &.invoice
          bg-img('invoice_3')
        &.special
          bg-img('special_3')

      .text
        display: table-cell
        font-size 12px
        width 56px
        vertical-align middle
        border-bottom (rgba(7,17,27,0.2))

  .foods-wrapper
    flex 1
    display: block
</style>



